<template>
  <div class="left-product">
    <div id="product-banner" class="top">
      <p class="title">背景图设置</p>
      <div class="banner-img">
        <img :src="bannerSrc">
        <div class="hidden-btn">
          <input type="file" class="file" @change="changeBanner">
          <p>更换</p>
        </div>
      </div>
      <p class="desc">
        宽度为1920像素，高度为300像素，JPG、PNG图片格式，2M以内为最佳显示效果
      </p>
      <div class="fail" v-show="display">
        <i class="icon-font fail-icon">&#xe605;</i>上传失败
        <p class="upload">请上传2M以内的图片</p>
      </div>
      <el-button type="primary" @click="save">保存</el-button>
    </div>
  </div>
</template>

<script>
  import myWebSiteApi from '../../../../../src/api/myWebSiteApi'
  export default{
    data () {
      return {
        bannerSrc: '',
        shopId: window.localStorage.shopId,
        advertSysId: -1,
        details: [],
        display: false,
        picUrl: '',
        detailStr: [],
        id: ''
      }
    },
    mounted () {
      this.getBannerImg({position: 'BACKGROUND'})
    },
    methods: {
      getBannerImg (params) {
        myWebSiteApi.getContactImg(params).then(res => {
          if (res.data.code === 0) {
            let data = res.data.data
            if (data !== null && data.length > 0) {
              this.bannerSrc = data[0].pircUrlWhole
              this.advertSysId = data[0].advertId
              this.picUrl = data[0].picUrl
              this.id = data[0].id
            }
          }
        })
      },
      changeBanner (e) {
        let files = e.target.files || e.dataTransfer.files
        if (!files.length) {
          return
        }
        let formData = new FormData()
        formData.append('picDir', 'shop')
        formData.append('picture', files[0])
        myWebSiteApi.uploadLogo(formData).then(res => {
          console.log(res)
          if (!res.data.code) {
            this.display = false
            this.bannerSrc = res.data.data.urlWhole
            this.advertSysId = 1
            this.picUrl = res.data.data.url
          } else {
            this.display = true
          }
        })
      },
      save () {
        this.detailStr = []
        this.detailStr.push({
          shopId: this.shopId,
          picUrl: this.picUrl
        })
        myWebSiteApi.productBanner({detailStr: JSON.stringify(this.detailStr)}).then(res => {
          if (!res.data.code) {
            window.editorApi.iframeLoad()
            this.$message.success('保存成功')
          } else {
            this.$message.warning(res.data.message)
          }
        })
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less" scoped>
  @import "../../../../assets/style/base";
  @import "../../../../assets/style/variable";
  .left-product{
    .top{
      float: left;
      width: 420px;
      padding: 20px;
      min-height: 760px;
      margin-right: 20px;
      background: #FFFFFF;
      box-shadow: 0 0 8px 0 rgba(0,0,0,0.08);
      border-radius: 4px;
      .title{
        font-size: 18px;
        color: #33475B;
        margin-bottom: 16px;
        font-family: PingFangSC-Medium;
      }
      .banner-img {
        position: relative;
        width: 420px;
        height: 120px;
        margin-bottom: 15px;
        background: url('../../../../assets/img/mark.jpg');
        background-size: 8px 8px;
        overflow: hidden;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          height: 100px;
        }
        .hidden-btn {
          display: none;
          position: absolute;
          bottom: 0;
          height: 40px;
          line-height: 40px;
          text-align: right;
          width: 100%;
          background-color: rgba(80, 109, 149, 0.60);
          color: @white;
          .file {
            width: 100%;
            height: 40px;
            line-height: 40px;
            opacity: 0;
          }
          p {
            position: absolute;
            top: 0;
            right: 0;
            z-index: -1;
            margin-right: 15px;
          }
        }
      }
      .banner-img:hover {
        .hidden-btn {
          cursor: pointer;
          display: block;
          z-index: 999;
        }
      }
    }
    .desc{
      font-size: 13px;
      color: #7C98B6;
      line-height: 21px;
      font-family: PingFangSC-Regular;
    }
    .fail{
      height: 35px;
      width: 380px;
      margin-top: 32px;
      padding: 20px;
      background: #FFEBF0;
      border: 1px solid rgba(255,255,255,0.80);
      border-radius: 4px;
      font-size: 14px;
      color: #404040;
      font-family: PingFangSC-Regular;
      .fail-icon{
        font-size: 18px;
        vertical-align: top;
        padding-right: 8px;
        color: #FF3B6E;
      }
      .upload{
        padding-left: 25px;
        font-size: 12px;
        color: #666666;
      }
    }
    .el-button{
      margin-top: 32px;
    }

  }
</style>



